import React from 'react';
import './SingleComment.css';
// import { useSelector } from 'react-redux';

const SingleComment = ({ item }) => {
  // const usename = useSelector((state) => state.userName);
  let time = item.date.split(' ')[0];

  return (
    <React.Fragment>
      <div className="comment arrow_box">
        <div className="author">{item.user_name}</div>
        <p style={{ fontStyle: 'italic' }}>
          {item.comment} <span className="date_comment">{time}</span>
        </p>
      </div>
    </React.Fragment>
  );
};

export default SingleComment;
