import React from 'react';
import './card-list.css';
import Card from '../card/card';
import Button from '@material-ui/core/Button';
import Masonry from 'react-masonry-css';
const cardList = ({
  blog,
  length,
  blogLength,
  increateOffset,
  statedFilter,
}) => {
  let blogss = blog.map((item) => <Card key={item.id} item={item} />);
  const breakpointColumnsObj = {
    default: 4,
    1100: 3,
    700: 2,
    500: 1,
  };
  return (
    <React.Fragment>
      <Masonry
        breakpointCols={breakpointColumnsObj}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        {blogss}
      </Masonry>

      <div>
        {statedFilter &&
        statedFilter.length === 0 &&
        length !== blogLength &&
        blogLength >= 10 ? (
          <Button onClick={increateOffset} variant="contained" color="primary">
            Load more
          </Button>
        ) : null}
      </div>
    </React.Fragment>
  );
};

export default cardList;
