import React from 'react';
import { url } from '../../api';
import AddComments from '../../containers/AddComments/AddComments';
import RenderComment from './commentsRender/CommentsRender';
import { useSelector } from 'react-redux';
import Login from '../Login/Login';
import Divider from '@material-ui/core/Divider';
// import reducer from '../../Store/reducer';
const BlogDetailsComment = ({ itemsBlog }) => {
  const items = useSelector((state) => state.loggedin);
  const logged = localStorage.getItem('loggedin');
  let imgB = url + `${itemsBlog.featured_image}`;

  return (
    <div>
      <div>
        <img alt="cover" className="cover_img" src={imgB}></img>
      </div>
      <Divider style={{ marginTop: '10px' }} variant="middle" />
      <RenderComment items={itemsBlog} />
      {logged ? (
        <AddComments items={itemsBlog} />
      ) : (
        <div className="no_comment">
          {' '}
          You must <Login /> to be able to comment!
        </div>
      )}
    </div>
  );
};

export default BlogDetailsComment;
