import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import BlogDetailsComment from '../blog-details/BlogDetailsComment';
import BlogDetailsContent from '../blog-details/BlogDetailsContent';
import { singleBlogUrl } from '../../api';
import axios from 'axios';
import { useState } from 'react';
import Loader from '../Loader/Loader';
import { withRouter } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    padding: '30px',
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    width: '100',
    color: theme.palette.text.secondary,
  },
  paperps: {
    padding: theme.spacing(2),
    textAlign: 'center',
    position: 'sticky',
    top: 0,
    color: theme.palette.text.secondary,
  },

  [theme.breakpoints.down('xs')]: {
    paperps: {
      position: 'unset',
      marginRight: '0px',
    },
  },
}));

const CardListDetails = (props) => {
  const classes = useStyles();
  const [blogItems, setblogItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const urlParam = props.match.params.slug;
  useEffect(() => {
    const loadData = () => {
      axios
        .get(singleBlogUrl + `/${urlParam}`)
        .then((res) => {
          setblogItems(res.data.blog);
          setLoading(false);
        })
        .catch((error) => {
          console.log('cancelled', error);
          setLoading(false);
        });
    };

    loadData();
  }, [urlParam]);

  return (
    <div className={classes.root}>
      <Grid container spacing={1}>
        <Grid item xs={12} sm={7} md={8}>
          <Paper className={classes.paper}>
            <div style={{ textAlign: 'left' }}>
              {loading ? (
                <Loader />
              ) : (
                <BlogDetailsContent itemsBlog={blogItems} />
              )}
            </div>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={5} md={4}>
          <Paper className={classes.paperps}>
            {loading ? (
              <Loader />
            ) : (
              <div>
                <BlogDetailsComment itemsBlog={blogItems} />
              </div>
            )}
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};

export default withRouter(CardListDetails);
