import React from 'react';
import { filteredBlogs } from '../../api';
import Axios from 'axios';
import { useDispatch } from 'react-redux';
import { FILTERED_BLOGS } from '../../Store/actions';
// import { blogUrl } from '../../api';
// import { useSelector } from 'react-redux';

const Tags = ({ tag, activeBorder }) => {
  const dispatch = useDispatch();

  // const isSelected = useSelector((state) => state.isSelected);
  // const [filterBlogs, setFilterBlogs] = useState([]);
  // const renderFiltered = () => {
  //   Axios.get(blogUrl).then((res) => console.log(res.data.blogs));
  // };

  const filterBlogs = () => {
    Axios.get(filteredBlogs + `/${tag.id}/blog`)
      .then((res) => {
        // setAllBlogtags(res.data.blog);
        dispatch({
          type: FILTERED_BLOGS,
          payload: activeBorder !== tag.id ? res.data.blog : [],
        });
      })
      .catch((err) => console.log(err));
  };

  return (
    <div
      style={{
        borderBottom:
          activeBorder === tag.id ? '1px solid white' : '1px solid transparent',
      }}
      onClick={filterBlogs}
    >
      <h4>#{tag.name}</h4>
    </div>
  );
};

export default Tags;
