import React from 'react';
import './BlogDetailContent.css';
// import { useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import MetaTags from 'react-meta-tags';
import { FacebookShareButton, WhatsappShareButton } from 'react-share';
import { InlineShareButtons } from 'sharethis-reactjs';
import { Helmet } from 'react-helmet';

const BlogDetailsContent = (props) => {
  let aaaa = props.itemsBlog.featured_image;
  console.log('https://devapp.thinktech.al/' + aaaa);
  // const items = useSelector((state) => state.state);
  const shareUrlEndpoint = props.location.pathname;
  const URL__BASE = 'https://devblog.thinktech.al/';

  console.log(props);
  function createMarkup() {
    return { __html: `${props.itemsBlog.content}` };
  }

  return (
    <div>
      <h1 className="main_title">ddfg</h1>
      <div className="title_content">
        By: <span className="title">dfg</span>
      </div>
      <Helmet>
        {' '}
        <meta
          property="image"
          content={'https://devapp.thinktech.al/' + aaaa}
        />
        <meta
          property="og:image"
          content={'https://devapp.thinktech.al/' + aaaa}
        />
      </Helmet>
      <div className="socials">
        <InlineShareButtons
          config={{
            alignment: 'center', // alignment of buttons (left, center, right)
            color: 'social', // set the color of buttons (social, white)
            enabled: true, // show/hide buttons (true, false)
            font_size: 16, // font size for the buttons
            labels: 'cta', // button labels (cta, counts, null)
            language: 'en', // which language to use (see LANGUAGES)
            networks: [
              // which networks to include (see SHARING NETWORKS)
              'whatsapp',
              'linkedin',
              'messenger',
              'facebook',
              'twitter',
            ],
            padding: 12, // padding within buttons (INTEGER)
            radius: 4, // the corner radius on each button (INTEGER)
            show_total: true,
            size: 40, // the size of each button (INTEGER)

            // OPTIONAL PARAMETERS
            url: URL__BASE + shareUrlEndpoint, // (defaults to current url)
            image: 'https://devapp.thinktech.al/' + aaaa, // (defaults to og:image or twitter:image)
            description: 'custom text', // (defaults to og:description or twitter:description)
          }}
        />
      </div>

      <div dangerouslySetInnerHTML={createMarkup()}></div>
    </div>
  );
};

export default withRouter(BlogDetailsContent);
