import React, { useState, useEffect } from 'react';
import './CommentRender.css';
import Axios from 'axios';
import { commentUrl } from '../../../api';
import SingleComment from './SingleComment/SingleComment';
import { useSelector } from 'react-redux';

const CommentsRender = ({ items }) => {
  const [comment, setComment] = useState([]);
  const [loadMore, setLoadMore] = useState(10);
  const [length, setlength] = useState(10);
  const commentValue = useSelector((state) => state.commentVal);
  // console.log('length', length);
  // console.log('comment', comment);

  const loadMoreComments = () => {
    setLoadMore((prev) => prev + 10);
  };

  const fetchData = () => {
    Axios.get(commentUrl + `?offset=0&limit=${loadMore}&blog_id=${items.id}`)
      .then((res) => {
        setComment(res.data.comments);
        setlength(comment.length);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    setTimeout(() => {
      fetchData();
    }, 500);
  }, [items.id, loadMore, commentValue]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div className="topSpot hg_of_comments">
      {comment.map((item) => (
        <SingleComment key={item.comment_id} item={item} />
      ))}
      {(length <= comment.length) &
      (length !== comment.length) &
      (comment.length >= 10) ? (
        comment.length !== 0 ? (
          <button
            className="topSpot loadmore_comments"
            onClick={loadMoreComments}
          >
            Load more
          </button>
        ) : (
          <div>No comments in this blog yet</div>
        )
      ) : null}
    </div>
  );
};

export default CommentsRender;
