import React from 'react';
import './card.css';
import { useHistory } from 'react-router-dom';
import Divider from '@material-ui/core/Divider';
import { url } from '../../api';

const Card = ({ item }) => {
  let history = useHistory();
  // console.log('111', item);

  const handleClick = () => {
    // console.log('card', item.id);

    history.push({
      pathname: `/${item.slug}`,
    });
  };
  return (
    <div onClick={handleClick} className="card-container">
      <img className="image_home" alt="blog" src={url + item.featured_image} />
      <Divider style={{ marginTop: '10px' }} variant="fullWidth" />
      <h3 className="title_home">{item.title}</h3>
      <p className="desc_card">{item.description}</p>
    </div>
  );
};

export default Card;
