import { LOG_IN } from './actions';
import { LOG_OUT } from './actions';
import { FILTERED_BLOGS } from './actions';
import { SUCCESS_COMMENT } from './actions';

const initialState = {
  loggedin: false,
  userName: '',
  filtered: [],
  successComment: false,
  commentVal: '',
  isSelected: false,
};
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case LOG_IN:
      // console.log('res', action.payload);
      const keyCommend = action.payload.data.access_token;
      action.payload.status === 200 && localStorage.setItem('loggedin', true);
      localStorage.setItem('user', action.payload.data.fullName);
      localStorage.setItem('authKey', keyCommend);
      return {
        ...state,
        loggedin: localStorage.getItem('loggedin'),
        userName: localStorage.getItem('user', action.payload.data.fullName),
      };

    case LOG_OUT:
      // const loggedOut = localStorage.setItem('loggedin', false);
      localStorage.removeItem('authKey');
      return {
        ...state,
        loggedin: localStorage.removeItem('loggedin'),
      };

    case FILTERED_BLOGS:
      const filterBlogs = action.payload;
      // const loggedOut = localStorage.setItem('loggedin', false);
      return {
        ...state,
        filtered: filterBlogs,
      };
    case SUCCESS_COMMENT:
      let commentValue = action.payload;
      return {
        ...state,
        successComment: !state.successComment,
        commentVal: commentValue,
      };
    default:
      return state;
  }
};

export default reducer;
