import React, { useState, useEffect } from 'react';
import '../App.css';
import CardList from './card-list/card-list';
import Axios from 'axios';
import CLoader from './ContentLoader/ContentLoader';
import { blogUrl } from '../api';
import Button from '@material-ui/core/Button';
import { useSelector } from 'react-redux';
const Blog = () => {
  const limit = 10;
  const [blog, setBlog] = useState([]);
  const [loader, setLoader] = useState(true);
  const [offset, setOffset] = useState(1);
  const [offsetFiltered, setOffsetFiltered] = useState(8);
  const [length, setlength] = useState(0);
  const statedFilter = useSelector((state) => state.filtered).slice(
    0,
    offsetFiltered
  );

  // console.log('filtered blog', statedFilter.length);
  // console.log('blog length', blog.length);
  // console.log('length length', length);
  const increateOffset = () => {
    getBlogs();
  };

  useEffect(() => {}, [statedFilter]);

  useEffect(() => {
    getBlogs();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const getBlogs = () => {
    Axios.get(blogUrl + `?offset=${offset}&limit=${limit}`)
      .then((res) => {
        let arr = [...blog];
        let updatedBlogs = arr.concat(res.data.blogs);
        setOffset((prev) => prev + 1);
        setBlog(updatedBlogs);
        setLoader(false);
        setlength(blog.length);
      })
      .catch((err) => {
        console.log(err);
        setLoader(true);
      });
  };

  return (
    <div style={{ padding: '30px 0px', position: 'relative' }}>
      {loader ? (
        <CLoader />
      ) : (
        <CardList
          blog={statedFilter.length === 0 ? blog : statedFilter}
          loader={loader}
          length={length}
          blogLength={blog.length}
          increateOffset={increateOffset}
          statedFilter={statedFilter}
        />
      )}

      {statedFilter.length !== 0 && offsetFiltered <= statedFilter.length ? (
        <Button
          style={{ position: 'absolute', bottom: '0', right: '45%' }}
          onClick={() => setOffsetFiltered((prev) => prev + 5)}
          variant="contained"
          color="primary"
        >
          Load more
        </Button>
      ) : null}
    </div>
  );
};

export default Blog;
