import React, { useState } from 'react';
import { useStyles } from './LoginPopupStyles';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import imgageLogIn from '../../../images/Landmark_Logo.png';
import Axios from 'axios';
import { LogInUrl } from '../../../api';
import { LOG_IN } from '../../../Store/actions';
import { useDispatch } from 'react-redux';
import WarrningAlert from '../../Alerts/AlertsWarring';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright Â© '}
      <Link color="inherit" href="https://thinktech.al/">
        ThinkTech
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const SignIn = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(false);
  const [errorLogin, setErrorLogin] = useState(false);

  const setRememberAuth = () => {
    setRemember(!remember);
  };
  const Login = (e) => {
    e.preventDefault();
    Axios.post(LogInUrl, {
      email: email,
      password: password,
      remember_me: remember,
    })
      .then((res) => {
        dispatch({ type: LOG_IN, payload: res });
      })
      .catch((err) => {
        setErrorLogin(true);
      });
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <div>
          <img alt="blogs" className={classes.title} src={imgageLogIn} />
        </div>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            onChange={(e) => setEmail(e.target.value)}
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            onChange={(e) => setPassword(e.target.value)}
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
          />
          <FormControlLabel
            control={
              <Checkbox
                value="remember"
                checked={remember}
                onClick={setRememberAuth}
                color="primary"
              />
            }
            label="Remember me"
          />
          {errorLogin ? <WarrningAlert /> : null}
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            onClick={Login}
            className={classes.submit}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link
                href="https://thinktech.al/#firstSection"
                target="_blank"
                variant="body2"
              >
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
};

export default SignIn;
