import React, { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { makeStyles } from '@material-ui/core/styles';
import { filteredBlogs } from '../../api';
import Axios from 'axios';
import { useDispatch } from 'react-redux';
import { FILTERED_BLOGS } from '../../Store/actions';

const ITEM_HEIGHT = 48;

const useStyles = makeStyles((theme) => ({
  root: {
    color: 'white',
  },
  [theme.breakpoints.up('sm')]: {
    BurgerMenu: {
      display: 'none',
    },
  },
}));

const BurgerMenu = ({ options }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [activeBorder, setActiveBorder] = useState('');
  const classes = useStyles();
  const open = Boolean(anchorEl);
  const dispatch = useDispatch();
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const filterBlogs = (id) => {
    setActiveBorder(id);
    Axios.get(filteredBlogs + `/${id}/blog`)
      .then((res) => {
        // setAllBlogtags(res.data.blog);
        dispatch({
          type: FILTERED_BLOGS,
          payload: activeBorder !== id ? res.data.blog : [],
        });
      })
      .catch((err) => console.log(err));
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={classes.BurgerMenu}>
      <IconButton
        aria-label="more"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleClick}
        className={classes.root}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            maxHeight: ITEM_HEIGHT * 4.5,
            width: '20ch',
          },
        }}
      >
        {options.map((option) => (
          <MenuItem key={option.id} onClick={() => filterBlogs(option.id)}>
            {option.name}
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
};

export default BurgerMenu;
