import React, { useEffect, useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { useStyles } from './HeaderStyles';
// import LogIn from '../Login/Login';
import Tags from '../Tags/tags';
import Axios from 'axios';
import { tagsUrl } from '../../api';
import { useHistory } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import { useSelector } from 'react-redux';
import LogoutButton from '../Logout/Logoutbutton';
import ThinkTech from '../../images/LogoThinkTech.png';
import { Link } from 'react-router-dom';
import BurgerMenu from '../BurgerComponent/BurgerComponent';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
// import { useSelector } from 'react-redux';
// import { useDispatch } from 'react-redux';
const AppBarNav = (props) => {
  const classes = useStyles();
  const [tags, setTags] = useState([]);
  // const statedFilter = useSelector((state) => state.filtered);
  const [activeBorder, setActiveBorder] = useState('');
  console.log(props);
  const [showTags, setShowTags] = useState(true);
  const isLogged = useSelector((state) => state.loggedin);
  let history = useHistory();
  // const dispatch = useDispatch();
  const handleClick = () => {
    // console.log('card', item.id);

    history.push({
      pathname: `/`,
    });
  };

  useEffect(() => {
    props.location.pathname !== '/' ? setShowTags(false) : setShowTags(true);
  }, [props.location.pathname]);

  useEffect(() => {
    Axios.get(tagsUrl)
      .then((res) => setTags(res.data.tags))
      .catch((err) => console.log(err));
  }, []);

  return (
    <div className={classes.root}>
      <AppBar className={classes.hder} position="static">
        <Toolbar>
          <Link to="/">
            <img alt="logo" className={classes.logoLM} src={ThinkTech} />
          </Link>
          <div className={classes.tagS}>
            {showTags
              ? tags.map((tag) => (
                  <Button
                    className={classes.btns}
                    key={tag.id}
                    onClick={() => {
                      activeBorder === tag.id
                        ? setActiveBorder('')
                        : setActiveBorder(tag.id);
                    }}
                    color="inherit"
                  >
                    <Tags tag={tag} activeBorder={activeBorder} />{' '}
                  </Button>
                ))
              : null}
          </div>

          <Typography className={classes.title}></Typography>
          {isLogged ? <LogoutButton /> : null}
          {props.location.pathname === '/' ? (
            <BurgerMenu options={tags} className={classes.BurgerMenu} />
          ) : null}
        </Toolbar>
      </AppBar>
    </div>
  );
};

export default withRouter(AppBarNav);
